import React, { lazy } from "react";
import { Route, Switch, useLocation } from "react-router-dom";

// project imports
import MainLayout from "./../layout/MainLayout";
import Loadable from "../ui-component/Loadable";

// dashboard routing
const DashboardDefault = Loadable(lazy(() => import("../views/dashboard")));

// utilities routing
// const UtilsTypography = Loadable(lazy(() => import("../views/utilities/Typography")));

// pages
const UtilsAsync = Loadable(lazy(() => import("../views/pages/Async")));
const UtilsConversation = Loadable(lazy(() => import("../views/pages/Conversation")));
const UtilsTelephony = Loadable(lazy(() => import("../views/pages/Telephony")));

//-----------------------|| MAIN ROUTING ||-----------------------//

const MainRoutes = () => {
  const location = useLocation();

  return (
    <Route
      path={[
        "/conversation",
        "/dashboard",
        "/telephony",
        "/modzy",
        "/shadow",
        "/",
      ]}
    >
      <MainLayout>
        <Switch location={location} key={location.pathname}>
          <Route path="/dashboard" component={DashboardDefault} />
          <Route path="/conversation" component={UtilsConversation} />
          <Route path="/telephony" component={UtilsTelephony} />
          <Route path="/modzy" component={UtilsAsync} />
          <Route path="/shadow" component={DashboardDefault} />
          <Route path="/" component={DashboardDefault} />
        </Switch>
      </MainLayout>
    </Route>
  );
};

export default MainRoutes;
